@import '../../assets/styles/core/base';
@import '../../assets/styles/core/setting';
@import '../../assets/styles/core/reset';
@import '../../assets/styles/core/common';

.addcar_view {
  padding: 0 pr2(20);
  li {
    padding: pr2(16) 0;
    height: pr2(50);
    box-sizing: border-box;
    display: flex;
    justify-content: space-between;
    align-items: center;
    color: #111E36;
    font-size: pr2(16);
    line-height: 1.2;
    position: relative;
    &:after {
      @include borderBottom(#E6EBF5);
    }
  }
  .cell_hd {
    flex: 1;
    display: flex;
    align-items: center;
    margin-right: pr2(30);
  }
  .cell_bd {
    display: flex;
    justify-content: space-between;
    align-items: center;
    overflow: hidden;
  }
  .car_name {
    @include ellipsis();
    width: 95%;
    padding-right: 4px;
  }
  .icon_arrow_r {
    @extend %icon_arrow_r;
  }
  input::placeholder {
    color: #C5CAD4;
    text-align: right;
  }
  input {
    text-align: right;
    border: 0;
    outline: none;
    padding: 0;
    font-size: pr2(16);
  }
  .icon_label {
    display: inline-block;
    width: pr2(16);
    height: pr2(16);
    margin-left: pr2(4);
    background: url('data:image/png;base64,') no-repeat;
    background-size: 100% 100%;
  }
  .car_license {
    width: pr2(25);
    height: pr2(25);
    margin-right: pr2(16);
    text-align: center;
    line-height: pr2(25);
    background: #0055FF;
    border-radius: 4px;
    color: #fff;
    font-size: pr2(15);
    font-family: PingFangSC-Regular;
  }
  .submit_btn {
    display: inline-block;
    width: 100%;
    margin-top: pr2(40);
    line-height: pr2(50);
    border-radius: 4px;
    color: #fff;
    font-size: pr2(16);
    background: #0055FF;
    text-align: center;
  }
}


.carnumbers_item {
  position: fixed;
  bottom: 0;
  left: 0;
  width: 100%;
  padding-left: pr2(12);
  padding-top: pr2(10);
  background: #d9dadf;
  a {
    display: inline-block;
    width: pr2(30);
    height: pr2(43);
    margin-right: pr2(10);
    margin-bottom: pr2(14);
    background: #fff;
    text-align: center;
    line-height: pr2(43);
    box-shadow: 0 2px 1px 0 #808185;
    border-radius: 6px;
    color: #111E36;
  }
}